      <!-- ครั้งที่ 4 -->
      <div class="mt-4">
        <div>
          <label class="text-base text-gray-600 font-bold">ครั้งที่ 4.1</label>
        </div>
        <div class="flex">
          <div class="pr-2">
            <label>
              จำนวนเปิดรับ
            </label>
            <input
              class="form-control"
              type="text"
              @blur="v$.q4Offer.$touch"
              v-model.number="q4Offer"
              :disabled="schedule.qRound !== 4"
            />
            <span
              class="errors"
              v-for="error of v$.q4Offer.$errors"
              :key="error.$uid"
              >{{ error.$message }} <br />
            </span>
          </div>
          <div class="pr-2">
            <label>
