<template>
  <div class="px-60">
    <base-card class="flex justify-end">
      <base-button @click="addStatusForm()"> + สถานภาพ</base-button>
    </base-card>
    <div class="grid-table-main ">
      <div
        class="
              grid grid-cols-3
              grid-table-header
            "
      >
        <div class="w-full h-auto  flex items-center justify-center">
          ชื่อสถานภาพ
        </div>
        <div class="w-full h-auto  flex items-center justify-center">
          สถานะ
        </div>
        <div class="w-full h-auto  flex items-center justify-center">
          แก้ไข
        </div>
      </div>

      <div class="grid-table-body">
        <div
          class="grid grid-cols-3 grid-table-row"
          v-for="status in statusList"
          :key="status.id"
        >
          <div class=" text-left my-auto ">
            {{ status.name }}
          </div>
          <div class=" text-center my-auto">{{ status.status }}</div>
          <div class=" text-center my-auto">
            <base-button mode="super-flat" @click="editStatus(status)"
              >แก้ไข</base-button
            >
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- Edit Dialog -->
  <data-modal
    :show="editStatusVisible"
    :deleteBtn="false"
    :title="mode === 'add' ? 'เพิ่มสถานะ' : 'แก้ไขสถานะ'"
    @close="editStatusVisible = false"
    @delete="trydeleteStatus()"
    @submit="submitStatusData()"
  >
    <div class="flex justify-center" v-if="formLoading">
      <base-spinner></base-spinner>
    </div>
    <form @submit.prevent class="py-4 pr-6" v-else>
      <div class="grid grid-cols-3 gap-4 ">
        <div
          class="col-span-1 text-right font-semibold flex justify-end items-center"
        >
          ชื่อ
        </div>
        <div class="col-span-2">
          <input
            class=" form-control"
            @blur="v$.statusName.$touch"
            :class="{
              errors: v$.statusName.$error,
            }"
            type="text"
            v-model="statusName"
          />
          <span
            class="errors"
            v-for="error of v$.statusName.$errors"
            :key="error.$uid"
            >{{ error.$message }}<br />
          </span>
        </div>
      </div>
      <div class="grid grid-cols-3 gap-4 ">
        <div
          class="col-span-1 text-right font-semibold flex justify-end items-center"
        >
          สถานะ
        </div>
        <div class="col-span-2 w-full">
          <select
            class="form-control"
            @blur="v$.statusStatus.$touch"
            :class="{
              errors: v$.statusStatus.$error,
            }"
            v-model="statusStatus"
            disabled
          >
            <option disabled :value="null">Please select one</option>
            <option value="open">open</option>
          </select>
          <span
            class="errors"
            v-for="error of v$.statusStatus.$errors"
            :key="error.$uid"
            >{{ error.$message }}<br />
          </span>
        </div>
      </div>
    </form>
  </data-modal>

  <toast
    :show="!!error"
    mode="error"
    @close="error = null"
    fixed
    :message="error"
  >
  </toast>
</template>

<script>
// import { db, functions } from "../../js/firebaseDb.js";
import DataModal from "../../components/UI/DataModal.vue";
// import SpinnerDialog from "../../components/UI/SpinnerDialog.vue";
import "firebase/firestore";
import useVuelidate from "@vuelidate/core";
import { required, minLength } from "@vuelidate/validators";
import LoadDataMixin from "../../mixins/loadData.js";
export default {
  mixins: [LoadDataMixin],
  components: { DataModal },
  setup() {
    return {
      v$: useVuelidate(),
    };
  },
  data() {
    return {
      // system
      editStatusVisible: false,
      error: null,
      mode: null,

      // form
      formLoading: false,

      // user
      statusId: null,
      statusName: null,
      statusStatus: null,
    };
  },
  validations() {
    return {
      statusName: {
        required,
        minLength: minLength(2),
      },
      statusStatus: {
        required,
      },
    };
  },
  computed: {
    statusList() {
      return this.$store.getters["students/status"];
    },
  },
  methods: {
    clearForm() {
      this.statusId = null;
      this.statusName = null;
      this.statusStatus = null;
    },

    async updateStatus() {
      await this.$store.dispatch("students/updateStatus", {
        id: this.statusId,
        name: this.statusName,
        status: this.statusStatus,
      });
    },
    async addStatus() {
      await this.$store.dispatch("students/addStatus", {
        name: this.statusName,
        status: this.statusStatus,
      });
    },

    trydeleteStatus() {
      alert("status is deleted");
    },
    async submitStatusData() {
      // check validation
      this.v$.$touch();
      if (this.v$.$error) return;

      this.formLoading = true;

      try {
        if (this.mode === "add") {
          if (this.v$.$error) return;
          await this.addStatus();
        } else if (this.mode === "edit") {
          await this.updateStatus();
        } else {
          throw Error(`mode ${this.mode} is not supported`);
        }
        this.editStatusVisible = false;
        this.clearForm();
        this.$store.dispatch("updateToast", {
          mode: "success",
          show: true,
          message:
            this.mode === "add" ? "เพิ่มข้อมูลสำเร็จ" : "แก้ไขข้อมูลสำเร็จ",
        });
      } catch (err) {
        this.error = err.message;
      }
      this.formLoading = false;
    },
    addStatusForm() {
      this.mode = "add";
      this.editStatusVisible = true;
      this.clearForm();
      this.v$.$reset();
      this.statusStatus = "open";
    },
    async editStatus(statusData) {
      this.mode = "edit";
      this.editStatusVisible = true;
      this.statusId = statusData.id;
      this.statusName = statusData.name;
      this.statusStatus = statusData.status;
    },
  },
};
</script>

<style scoped>
.form-row {
  @apply pb-3;
}
.std-info-grid {
  @apply grid grid-cols-4 py-6;
}

.std-info-grid h3 {
  @apply col-span-4 md:col-span-1 pb-4;
}

.std-info-data {
  @apply col-span-4 md:col-span-3;
}

